<p>Sabemos que somos muchos los que tenemos que sumarnos para hacer de este sueo un movimiento que tenga la capacidad de recorrer el mundo. Por eso, El Sueo de Chocolate pronto abrir sus puertas para que todos aquellos que quieran contribuir con su conocimiento, su experiencia y su tiempo, puedan encontrar en Luker Chocolate, el lugar para hacerlo.</p>
