    date: 'JULIO 2019',
    content: `<p>Tres de nuestros colaboradores se fueron a la regin del Huila a vivir como cacaocultores y de ah naci esta serie. En esta primera historia Jos Maldonado, Analista de Abastecimiento sostenible, nos cuenta su experiencia.</p>
<p>Desde muy pequeo, el campo y yo hemos sido inseparables. Mi abuelo paterno es veterinario y ha sido productor de leche toda su vida; mientras que mi abuelo materno es caficultor, como lo fueron sus antepasados por varias generaciones. Siempre me he sentido orgulloso del campo colombiano y me he identificado como parte de este; tanto as, que ms de una vez he dicho con la cabeza en alto yo soy del campo.</p>
<p>Cre tener experiencia, pero despus de pasar dos semanas acompaando a diferentes familias cacaocultoras del Huila, me di cuenta de que no poda estar ms equivocado. Para ser del campo hace falta valenta y sacrificio, pero ante todo, hace falta tener un corazn gigante y estar profundamente enamorado de la tierra y sus tradiciones.</p>
<p>En esta travesa conoc muchas personas, pero las historias que a continuacin voy a contar, pertenecen a personas que me dejaron grandes lecciones de vida.</p>
<p>El primero fue Pedro Nel Crdoba; l nos recibi en el aeropuerto de Neiva y nos dio la bienvenida. Este opita de acento cantado es el representante de Desarrollo Agrcola de Luker Chocolate para los departamentos de Huila, Tolima, Putumayo y Caquet. No hay proveedor, finca o asociacin donde no lo saluden con un fuerte abrazo y con la calidez de un hermano que vuelve a casa. Pedro conoce y entiende su tierra a la perfeccin, pues lleno de orgullo recuerda frecuentemente que l creci en el campo y que sus padres lo educaron a l y a 5 hermanos con los frutos de 1 hectrea de cacao. l sabe bien lo que es luchar y trabajar por una finca que, por ms pequea que sea, es la esencia y el fruto de vida que mantiene a miles de familias campesinas en Colombia.</p>
<p>El equipo de trabajo liderado por Pedro estaba listo para emprender nuestra travesa de 2 semanas por este hermoso departamento. Comenzamos en el municipio de Rivera; en este lugar visitamos tres familias pertenecientes a la Asociacin de Productores de Cacao del Municipio de Rivera (). Tuve la fortuna de visitar la finca de Doa Carmen, una mujer de 82 aos, pero con corazn y alma de 20. Ella es la matrona de su familia y se enorgullece de ser la cabeza de cuatro generaciones, pero an ms, de ver a sus hijos teniendo xito gracias a la vida y educacin que les pudo brindar con las 3 hectreas de cacao que trabaj por tantos aos con su difunto esposo.</p>
<p>La casa de Doa Carmen queda justo al lado de su plantacin de cacao. Es un hogar lleno de amor y detalles; se pueden ver diferentes imgenes catlicas y fotografas con su familia. En la visita, recorrimos todo su cultivo y, planta por planta, esta mujer nos iba mostrando lo duro que trabaja por sus rboles de cacao y lo mucho que los conoce.</p>
<p>El da siguiente fue revelador, pues se deba hacer la recoleccin y el desgranado de las mazorcas de cacao. Dos trabajadores eran los encargados de recolectarlas y adems haba un equipo de desgranado, del cual nosotros hacamos parte. Lo que pareca una tarea sencilla, termin siendo extenuante. En el lapso de una hora tuve que parar dos veces, ponerme de pie, estirar y esperar a que se me pasara el dolor de espalda. Mientras tanto, Doa Carmen, con sus 82 aos, no se quej ni una sola vez y solo se detuvo para almorzar. Ella simplemente se rea al vernos luchar para completar la tarea. As aprend que la fortaleza y el carcter de la gente del Huila son admirables.</p>
<p>Nuestra siguiente parada fue el municipio vecino de Campoalegre, la capital arrocera del Huila. Una regin con vocacin agrcola y marcada por unos valles hermosos con diversos tipos de cultivos. En este lugar me marc particularmente una finca en una zona montaosa, en donde comienza a predominar el caf. All conocimos a Doa Concha, su hijo Gentil y a quien me dara una gran leccin de vida: Samuel. Este hombre de tez morena, buen bigote, una alegra contagiosa y 35 aos mal calculados, tiene su propio cultivo de maz y cilantro en la finca y fue uno de nuestros guas.</p>
<p>Samuel es ciego desde hace 8 aos, pero nosotros no podamos creerlo, y no fue porque dudramos de su palabra, sino porque la forma en la que se ubica dentro de la finca es irreal. Nunca se pierde e incluso transita caminos muy peligrosos completamente solo. Lo ms increble de este hombre es su actitud. En un momento le preguntaron si haba sido muy difcil recobrar su vida despus de perder la vista y respondi, tranquilamente, un poquito, no ms.</p>
<p>Despus de recorrer esta regin del Huila, emprendimos un camino de unos 86 kilmetros hacia el sur del departamento. Recorrimos las orillas del Ro Magdalena y la gigantesca Represa del Quimbo hasta nuestro ltimo destino: Garzn. Este municipio es un importante foco cafetero de Colombia y una gran despensa agrcola para la regin; adems, se ha convertido en un punto muy importante de recoleccin de cacao para Luker Chocolate. Aqu fue donde tuve el encuentro ms significativo y quizs el que ms me inspir a seguir trabajando por los cacaocultores de Colombia.</p>
<p>En el penltimo da de nuestra travesa, fuimos a la vereda Majo. En este lugar conocimos la finca de Don Ismael, un hombre de aproximadamente 80 aos y oriundo de la regin. Nuestro anfitrin principal fue Chucho, uno de sus hijos quien cuida de l y lo ayuda con las labores de la plantacin de cacao. Tuvimos la oportunidad de ayudarlos en la jornada de trabajo recogiendo las mazorcas de cacao maduras durante toda la maana. Fue hasta el final de la jornada, y despus de disfrutar de un sancocho campesino, que lleg el personaje que tanto me marc.</p>
<p>Por la parte trasera de la casa entr un nio de unos 12 aos, pelirrojo y lleno de pecas. Vesta un jean hasta las rodillas, una esqueleto blanco, una resortera cruzada en su pecho y unas botas de caucho. Traa un balde lleno de pescados pequeos que acababa de atrapar en un ro cercarno. Pareca un Tom Sawyer colombiano, un nio aventurero de esos en peligro de extincin. Sin pensarlo, se acerc a nosotros a mostrarnos la pesca del da y nos comenz a hablar de todo lo que haca despus de ir al colegio. Su nombre es Wilmer y es el nieto de Don Ismael. Me hizo un recorrido para mostrarme su cultivo de cebolla, unos patos que est criando y finalmente, lleno de orgullo, me mostr su propio rbol de cacao. En ese momento pens: el campo colombiano s tiene un gran futuro y por nios como l es que debemos seguir trabajando.</p>
<p>Wilmer es un nio emprendedor y curioso. A muy temprana edad empez a ayudar a su mam, pues su padre los abandon cuando l era solo un beb. Pero esto nunca lo detuvo. Su forma de ver la vida, de apreciar el agro, sus ganas de salir adelante y el modo en el que ayuda a su familia, me marcaron de una forma indescriptible.</p>
`,
