    title: 'JUNTOS POR LAS COMUNIDADES CACAOCULTURAS DE COLOMBIA',
    date: 'AGOSTO 2019',
    content: `<p>Mejorar el campo e impulsar a las comunidades cacaocultoras de Colombia es el motor de El Efecto Cacao, una alianza entre Luker Chocolate, Fundacin Luker, USAID Colombia, Enel-Emgesa, Fundacin Saldarriaga Concha y la Universidad EAFIT, que trabaja por el desarrollo rural e inclusivo de las comunidades que cultivan cacao en el Bajo Cauca, Urab y Centro Sur del Huila.</p>
<img src="/static/media/el-efecto-cacao.bc6f7bc9.jpg" alt="Efecto Cacao">
<p>Para hacer realidad El Efecto Cacao, es necesario que el sector privado y el sector pblico trabajen de la mano. El sector privado tiene una visin de largo plazo, mayor estabilidad en las polticas y estrategias, capacidad de generar valor a travs del mercado, contactos con voluntarios internacionales y, en nuestro caso, Luker Chocolate tiene en su ADN el inters por el productor de cacao, sus familias y sus comunidades.</p>
<p>La Agencia de los Estados Unidos para el Desarrollo Internacional, USAID, tiene un modelo llamado GDA (Alianzas de Desarrollo Global), el cual proporciona ayuda efectiva a los pases en desarrollo a travs de asociaciones pblico-privadas. Para ejecutar este modelo, el pas debe demostrar que tiene empresas privadas con la capacidad de invertir recursos en fines comunitarios, conectando las soluciones con actividades econmicas que produzcan valor compartido. Aqu es donde todos los actores de esta alianza juegan un papel fundamental.</p>
<p>Luker Chocolate y la Fundacin Luker buscaron recursos en USAID para contribuir a El Sueo de Chocolate, una visin de largo plazo para liderar desarrollo social y econmico en las regiones productoras de cacao. La Fundacin Luker, adems; brinda de manera transversal sus focos de inters en educacin y emprendimiento. A la iniciativa se sumaron la Universidad EAFIT con su experiencia en el mejoramiento de las asociaciones productivas para generar desarrollo en los territorios rurales, la Fundacin Saldarriaga Concha con la Universidad del Norte y su Programa ReHaSer, el cual busca generar habilidades de resiliencia en los territorios ms marginados del pas, y Enel-Emgesa que desarrolla proyectos sociales y ambientales en el rea de influencia de la Represa del Quimbo.</p>
<h3>PERO, Y CMO FUNCIONA EL EFECTO CACAO?</h3>
<p>El centro del acuerdo son los pequeos productores de cacao que, beneficiados con un impacto social y econmico, llevan estabilidad a su familia y su comunidad. A este pequeo productor lo apoyamos, pero sin un sentido paternalista. El primer compromiso parte de l, quin tiene que invertir recursos en su finca y realizar todas las prcticas de mejoramiento.  La inversin total es de 35 millones de dlares, los cuales son puestos por todos los actores que forman parte de esta alianza y sern ejecutados en los prximos 5 aos, tiempo que dura el proyecto.</p>
<p>Luker Chocolate pone a disposicin los cultivos empresariales que tiene en el Urab y en el Huila, como un modelo de desarrollo rural inclusivo para que los productores reciban soporte tcnico, apropien mejores tecnologas y aprovechen los canales de comercializacin.</p>
<img src="/static/media/paint-window.40d79a05.jpg" alt="Painting window">
<p>El acuerdo aporta todas las herramientas que, por la experiencia con las comunidades cacaocultoras, sabemos que son tiles: capacitacin, extensin agrcola, podas de rehabilitacin, semillas de sombra transitoria, anlisis de suelos, fertilizantes, asesora administrativa, tcnica y comercial para las asociaciones; soporte en emprendimiento, educacin formal para nios, jvenes y adultos; apoyo psicolgico y, finalmente; la relacin institucional para gestionar otros apoyos segn la necesidad puntual de la poblacin beneficiada.</p>
<h3>A DNDE QUEREMOS LLEGAR?</h3>
<img src="/static/media/cocoa-friends-4.28b994d9.jpg" alt="Efecto Cacao People">
<p>La meta es incrementar la productividad de 1.340 hectreas de cacao, fortalecer 17 asociaciones cacaocultoras, acompaar 20 iniciativas de emprendimiento y fortalecer las competencias acadmicas y socio-emocionales de 1.561 personas.</p>
<p>Estamos convencidos de que este tipo de alianzas entre el sector pblico y el privado son un claro ejemplo del alcance que pueden tener las buenas prcticas de cooperacin y los grandes resultados que se pueden generear cuando se trabaja de la mano. Sin duda alguna, El Efecto Cacao cambiar la vida de muchas familias y ser un ejemplo de desarrollo rural para el mundo.</p>
<p>Los invitamos a que estn pendientes de todos los avances del proyecto. No olviden dejarnos sus comentarios aqu abajo.</p>
`,
    autor: { name: 'JULIA OCAMPO', avatar: 'https://media.licdn.com/dms/image/C4E03AQFUGeFloCcVFw/profile-displayphoto-shrink_800_800/0?e=1581552000&v=beta&t=NJf7kTdRi6NTIdp250nSBA81HPnpIPN6KQYUr1Tl0uI', details: 'COP The Cocoa Effect at Fundacin Luker', description: '', linkedin: ['https://www.linkedin.com/in/julia-ocampo-4725376/', 'Julia Ocampo'] },
