    breads: [{ href: '/blog', name: 'Blog' }, { href: '/blog/lo-que-no-sabias', name: 'Lo que no sabias' }],
    url: 'un-chocolate-al-dia-mantiene-alejado-al-doctor',
    title: 'UN CHOCOLATE AL DA, MANTIENE ALEJADO AL DOCTOR',
    cover: 'finished-product-title.jpg',
    date: 'AGOSTO 2019',
    content: `<p>Pocas personas conocen los grandes beneficios que trae el consumo regular de cacao. La gran mayora de los consumidores estn convencidos (algunos por recomendaciones mdicas), que el cacao y/o el chocolate, son perjudiciales para la salud. Con el objetivo de solucionar esta discrepancia, el rea de Investigacin de Luker Chocolate, en asocio con centros de investigacin de Colombia y de Italia (University of Molise  Campo Basso, Sannio Tech  Benevento, y University of Brescia) caracterizaron los cacaos de Colombia, Ecuador y Per, y desarrollaron una variedad de diseos experimentales por ms de cuatro aos, incluyendo estudios preclnicos y clnicos con 50 voluntarios. Una vez obtenidos los resultados, los discutimos con un grupo formado por los ms reconocidos cardilogos, nutricionistas, nutrilogos, dermatlogos, deportlogos, etc.</p>
<p>Hoy, tenemos suficiente evidencia cientfica y tcnica para poder afirmar con certeza que el consumo regular de cacao ayuda a mejorar y a proteger la salud de las personas. Pero cunto chocolate debemos consumir? Nuestra recomendacin es dos porciones de chocolate en polvo al da o una de chocolate amargo durante al menos un mes. Esto lo ayudar a:</p>
<h3>PROTEGER LA SALUD CARDIOVASCULAR</h3>
<p>Consumir cacao ayuda a reducir el colesterol total y el colesterol malo (LDL), mientras que aumenta el colesterol Bueno (HLD) y reduce o regula la relacin omega 6 y omega 3, ayudando a mejorar su salud cardaca y disminuyendo los riesgos de enfermedades de este tipo. Su corazn se har ms fuerte si adems de consumir cacao, empieza a adquirir hbitos de vida saludable en su alimentacin disminuyendo el consumo de grasas animales y de harinas, mientras aumenta el consumo de cereales y verduras. Adems, le recomendamos hacer ejercicios fsicos como caminar hasta lograr 10.000 pasos diarios.</p>
<h3>PROTEGER LA SALUD GSTRICA E INMUNOLGICA</h3>
<p>El cacao contiene fibras (insolubles y solubles) que ayudan a mejorar el movimiento intestinal, permitiendo evacuaciones normales que ayudan a eliminar las toxinas producto del metabolismo de las grasas y los carbohidratos. Estas fibras tienen una caracterstica prebitica que alimentan a los microorganismos probiticos que mantienen la salud gstrica. Es importante recordar que la salud inmunolgica est condicionada en gran parte por la salud gstrica, lastimosamente los colombianos tienen problemas gstricos debido a la poca ingesta de fibras que provienen de cereales y verduras.</p>
<p>Tip: consuma chocolate y verduras a diario, y notar cmo mejora su salud gstrica e inmunolgica.</p>
<h3>MEJORAR EL ESTADO DE NIMO</h3>
<p>El cacao posee unas molculas que actan como neurotransmisores de mensajes en el sistema nervioso central; una de ellas es la feniletilamina, mejor conocida como la molcula del amor, pues se ha demostrado que el cerebro de una persona enamorada contiene grandes cantidades de esta sustancia que podra ser la responsable de ciertas sensaciones como la excitacin, taquicardia, enrojecimiento, entre otras. Este neurotransmisor desencadena, a su vez, la accin de otros neurotransmisores llamadas molculas de la felicidad como la serotonina (genera sentimiento de amor propio y de respeto por los dems), la oxitocina (genera sentimiento de amor por otras personas y vinculacin afectiva) y la dopamina (generadora de alegra y sentimiento de euforia).</p>
<p>Tip: no se amargue la vida, tmela suave y tambin tome chocolate, el antidepresivo por excelencia. Realmente funciona.</p>
<h3>PRODUCIR O ACTIVAR SISTEMAS ENERGTICOS DEL CUERPO</h3>
<p>S, consumir cacao ayuda a producir energa o activar la energa existente en el cuerpo. Su contenido de potasio y la activacin de protenas importantes en el proceso de produccin de energa hacen del cacao un alimento clave en las maanas para mantenerse energizado durante todo el da. Lo mejor?, no contiene dosis excesivas de cafena.</p>
<h3>MANTENER LA SALUD GENERAL DE TODO EL CUERPO</h3>
<p>Esta parece una frase bajada del video de algn youtuber. Parece increble o un tanto atrevido decirlo, o ms afirmarlo. Venga se lo explico. Cuando respiramos o nos exponemos al sol, nuestro cuerpo libera sustancias que lo oxidan y que son muy reactivas y peligrosas. Se llaman radicales libres y son molculas incompletas que van buscando por nuestro cuerpo electrones para completarse. Estos radicales son las causantes de la gran mayora de enfermedades crnicas no transmisibles que son las que ms muertes causan en el mundo, tales como: enfermedades cardacas, cncer, diabetes y obesidad. Aunque nuestro cuerpo produce sus propios antioxidantes que neutralizan rpidamente esos radicales libres o sustancias oxidantes, el estrs, no comer a tiempo, fumar, beber alcohol en exceso y no hacer ejercicio, disminuyen la produccin de antioxidantes naturales y empiezan a predominar nuevamente los radicales libres. Traducido al espaol: los malos hbitos nos oxidan. Afortunadamente existen alimentos como el t, las uvas, los arndanos y especialmente el chocolate, que aportan niveles de antioxidantes al cuerpo impidiendo que esto pase. En el caso especfico del cacao, este cuenta con una sustancia llamada NFR2, que es una protena que reactiva los genes que producen nuestras propias sustancias antioxidantes, aumentando an ms la proteccin contra enfermedades crnicas no transmisibles.</p>
<p>Por otro lado, el cacao posee cantidades importantes de minerales que facilitan el desarrollo de reacciones biolgicas como el potasio, que intervienen en las reacciones de produccin de energa y regula el ritmo cardaco; el calcio que ayuda a la transmisin del impulso nervioso adems de proteger contra la osteoporosis; el hierro, que ayuda a combatir la anemia; y el zinc, que ayuda al sistema inmunolgico, al desarrollo cognitivo de los nios y a madurar y mantener la actividad sexual a travs de los aos.</p>
<p>Como ves, las bondades del cacao van ms all de su sabor inigualable y de su poder de transformar las regiones cacaoteras de nuestro pas. Te invitamos a que lo consumas a diario y a que nos cuentes en los comentarios si pudiste evidenciar cambios en tu salud.</p>
    `,
