    url: 'arte-para-sanar',
    cover: 'healing-with-art.jpg',
    title: 'ARTE PARA SANAR',
    date: 'SEPTIEMBRE 2019',
    content: `<p><small><b>Nota del editor:</b> El ao pasado el equipo de Transhuella trabaj con nosotros implementando un proyecto artstico que uni la pintura y la fotografa para reconocer la herencia cultural de las comunidades aledaas en nuestro cultivo de cacao en Necocl y ayudar a sanar las heridas de guerra. A partir de su experiencia, construyeron un libro lleno de historias y hoy te traemos la primera.</small></p>
<q>Forma bien a la gente para que pueda marcharse, tratales mejor para que no quieran hacerlo</q>
<small> - Richard Branson, Fundador Virgin</small>
<p></p>
<p>Sin duda alguna, Luker Chocolate, en nuestro imaginario, es un empaque amarillo que contiene el chocolate que tomamos desde que dejamos la leche materna. Esa bebida que incluso en los climas mas calientes se sirve en la mesa de las familias colombianas, que reune el amor genuino y expresiones como Ay, Dios mio, Esto lo hicieron con lena robada, al quemarse.</p>
<q>Creo que en ningun lugar se sirve el chocolate tan caliente como en Colombia.</q>
<p>Hay algo mas alla en Luker Chocolate. Hay una historia de tradicion y de exito, de innovacion y de pertenencia, una historia de responsabilidad que pocos conocen.</p>
<p>Una empresa con un amplio portafolio de productos, suficiente para abastecer todo lo que necesita un hogar, para darle vida y color a nuestras tienditas de barrio. En Colombia podemos caminar unos pocos metros para comprar lo que nos hace falta mientras saludamos a los vecinos.</p>
<p>Hay un producto estrella con un aroma unico que hipnotiza varias cuadras a la redonda de las oficinas principales en ciudad como Bogota: el olor a CACAO.</p>
<p>A partir del cacao, Luker Chocolate ha construido la columna vertebral de su programa de responsabilidad social, uno que ha sido premiado y reconocido a nivel mundial.</p>
<p>La responsabilidad social o el valor compartido son sinonimos de exencion de impuestos y campanas de marketing que muestran el lado amable de las empresas. Asi lo percibimos los ciudadanos comunes, como una excusa de las empresas que mas que ayudar a quienes se encuentran en su area de impacto y devolver a la sociedad lo que han logrado, buscan disminuir la carga impositiva.</p>
<p>Sin embargo, en Transhuella sabemos que somos mas los que hacemos las cosas bien y actuamos con sentido social; que somos mas quienes producimos pensando en devolverle a la sociedad colombiana y su territorio todo lo que nos han dado.</p>
<p>Por eso hemos emprendido esta tarea con aquellos dispuestos a abrir sus puertas para dar a conocer lo que hay detras de cada uno de sus productos: el amor y el compromiso con sus origenes y la sociedad.</p>
<p>Entender que los trabajadores, empleados, la materia primera y el entorno, son tan importantes como sus propietarios; de eso de tratan las empresas sostenibles.</p>
<p>No es acaso responsabilidad social involucrar a otros actores para enamorarlos, primero, de lo que significa un esfuerzo individual para luego convertirlo en una causa comun?. Como sociedad la responsabilidad es compartida, todos tenemos algo que aportar.</p>
<p>Para nuestra sorpresa, hay un equipo detras de esa gran marca pensando no en como mostrar su aporte a la sociedad (mas alla de la energia diaria que nos dan con su chocolate) sino en como impactar la vida de las manos que hacen realidad El Sueno de Chocolate.</p>
<img src="/static/media/nina.9b48b347.jpg" alt="girl">
<p>Luker Chocolate es sinonimo de bendiciones y oportunidades, asi lo dicen Rosalba y Silvia, madre e hija. Silvia trabaja con Luker Chocolate, y Rosalba, a pesar de no hacerlo, ha sentido el impacto positivo en la region desde la llegada de una empresa que se quedo en sus corazones. Una empresa que le aposto a desarrollar su negocio de la mano de la comunidad que rodea su cultivo empresarial de cacao en Necocli, no solo a quienes trabajan con ellos de forma directa.</p>
<p>Sin duda alguna, la presencia de Luker Chocolate le ha dado vida a esta comunidad y ha impulsado sus suenos, incluso de personas que no se relacionan de forma directa con su empresa.</p>
<p>Construir un pais es arriesgarse como lo hace Luker Chocolate, un ejemplo para el sector empresarial colombiano.</p>
<p>El chocolate tiene, seguro, celebraciones en el mundo entero Pero solo un lugar magico entiende la importancia de celebrar el fruto de donde proviene un alimento, un dulce, un producto con facetas y usos tan diversos como sus colores.</p>
<p>Caribia tiene 438 habitantes, 438 voces que cantan en la alborada del Festival del Cacao:</p>
<q>Viene que ya viene, viene el Festival, Crece la cosecha vamo a celebrar<br>
<i>Viene que ya viene, viene el Festival se unen las familias, todas por igual.</i></q>
<p>El Festival del Cacao es un dia mas en el que Luker Chocolate se vuelca por completo a construir con la comunidad, a pintar la cara de los ninos con sonrisas, compartir una chocolatada
y un sancocho en los que todos participan por igual; llevar un equipo de profesionales de las mas altas calidades humanas, para recordarle a la comunidad de Caribia que sin ellos el chocolate no tendria el mismo sabor.</p>
<img src="/static/media/abrazo.55a56497.jpg" alt="Festival Cacao">
<p>En Transhuella estamos orgullosos de colaborar con Luker Chocolate, una empresa que crece de la mano de un pais.</p>
<p>Queremos ser participes de la transformacion de Colombia.</p>
`,
    autor: { name: 'VALENTINA RAMREZ', avatar: 'https://pbs.twimg.com/profile_images/1132303793331113984/tmfrgfNK_400x400.jpg', details: 'Director at Transhuella', description: 'Professional in Government and International Relations with emphasis in Public Management, 2 years of experience in the formulation and coordination of projects in public and non-governmental entities.', linkedin: ['https://www.linkedin.com/in/valentina-ramirez-zapata-58616654/', 'Valentina Ramirez Zapata'] },
