    title: 'CHOCOLATE  ESPARCIENDO EL AMOR POR EL MUNDO',
    cover: 'stand-banner.jpg',
    date: 'OCTUBRE 2019',
    content: `<p>Hacer un viaje a Luker Chocolate en Colombia y vivir #LukerExperience en carne propia, result ser una experiencia reveladora para la duea de Le Salon Du Chocolat, Dawn Shrives. De su visita, regres con un nuevo aprecio por la produccin de chocolate y llena de ideas sobre cmo su empresa, ubicada en Chichester, Sur de Inglaterra, puede apoyar el trabajo que se est haciendo en ese pas.</p>
<p>Establecido en 2004, Le Salon Du Chocolat es un estudio de chocolate en el cual sus visitantes pueden poner a prueba sus habilidades con talleres y fiestas temticas. Dawn tambin dicta charlas y demostraciones para organizaciones y escuelas locales.</p>
<p>Pero la pasin de Dawn es la calidad del chocolate, desde la fuente de granos de cacao hasta el producto terminado. Ella explica:</p>
<p>En particular durante los ltimos 20 aos, a medida que la demanda por los granos de cacao ha aumentado, forzando el incremento del precio, muchas de las grandes marcas de chocolate que ahora amamos estn usando menos cacao, completando sus productos con grasas vegetales alternativas ms econmicas y con harina de trigo. Por lo tanto, los productos terminados se describen como barras o con sabor a chocolate, pues ya no contienen suficiente cacao como para poderlos llamar chocolates.</p>
<p>Los chocolateros artesanales estn volviendo a usar chocolate que contiene solo los ingredientes bsicos necesarios. La cobertura (discos de chocolate) que uso en mis talleres es hecha en Blgica, y resulta de una mezcla de granos de cacao provenientes por lo general de frica, que es la fuente de alrededor del 70% de todos los granos de cacao que se cosechan. Sin embargo, tambin uso coberturas hechas con granos de cacao de otras regiones de cultivo, como Colombia, Per y Costa Rica que, cuando son de origen nico, en lugar de ser mezclas, le dan al chocolate un sabor especfico de esa regin.</p>
<p>Dawn fue uno de los veinte chocolateros que viajaron a Luker Chocolate, en una visita para experimentar de primera mano cmo se cultiva y cosecha el cacao, cmo se convierte en chocolate, y cmo Luker apoya a los cacaocultores, a sus familias y a las comunidades locales. Luker, una empresa familiar colombiana establecida en 1906, es una compaa proveedora de cacao y fabricante de chocolate que cree que la produccin de cacao puede producir un impacto social positivo: creemos que el chocolate hace del mundo un lugar mejor. Luker Chocolate compra casi el 40% del cacao total producido en Colombia, y todos sus granos de cacao cumplen con sus estndares de Fino de Aroma; un cacao fino que corresponde a solo el 8% del cacao total del mundo.</p>
<p>El viaje me abri los ojos, dijo Dawn. Luker es una de las empresas ms grandes de Colombia. Lo que diferencia a Luker de otras compaas es que fabrica en Colombia sus productos, desde los granos hasta las barras, lo que quiere decir que usa azcar, leche y mano de obra colombianas, y exporta el producto terminado en lugar de solo los granos de cacao. De modo que los productores de leche y de caa de azcar tambin se benefician directamente.</p>
<p>La travesa de chocolate de Dawn inici apenas aterriz en Bogot, con un desayuno tradicional de chocolate caliente, almojbana y queso en la fbrica de Luker. A esto le sigui una cata de chocolate.</p>
<p>Cada una de las etapas de cmo se cultivan, cosechan y procesan los granos en nuestro chocolate tiene un gran impacto en el sabor final, explic Dawn. Un ejemplo es saborear un chocolate hecho con granos tostados a 130 grados y compararlo con uno tostado a 160 grados. La diferencia en los grados haba hecho que el chocolate fuera casi incomible!.</p>
<p>El grupo vol a Granja Luker, un centro de capacitacin e investigacin alrededor del cacao que entrena a los agricultores colombianos para mejorar su productividad y que tambin les vende material vegetal. Desde su fundacin en 1962, Granja Luker ha educado a 700 agricultores al ao para que siembren y cosechen granos de cacao Fino de Aroma.</p>
<p>Algo memorable para Dawn fue su visita a Necocl, Antioquia, ubicado muy cerca del paso hacia Panam. Hace algunos aos Luker sembr 600.000 nuevos rboles de cacao en 550 hectreas de esa rea, creando 180 trabajos nuevos en un rea conocida por haber sido vctima de conflicto armado. Luker ha apoyado en gran medida a los habitantes de las comunidades aledaas al cultivo, incluyendo las tres escuelas del rea. Dawn dijo:</p>
<p>Visitamos dos de las escuelas dentro de la plantacin de cacao y nos pidieron que les llevramos regalos educativos a los nios y les contramos algo sobre nuestras vidas. Yo llev tarjetas postales de Chichester y de Wittering para mostrarles a los nios dnde vivo, y para que las pudieran usar para escribirme y luego yo llevarlas a nios de las escuelas locales de mi ciudad. Me encantara poder establecer una conexin entre las escuelas de West Sussex y de Colombia.</p>
<p>Caribia, una de las comunidades aledaas al bosque de cacao, se ha transformado gracias al Sueo de Chocolate de Luker. Este proyecto naci como la visin de largo plazo de Luker Chocolate para liderar desarrollo social y econmico en las regiones productoras de cacao. Luker empez a preguntarse sobre su responsabilidad como compaa. Qu ms podan llevar al campo, a las personas, a los nios y a los vecinos? El Sueo de Chocolate, junto a voluntarios y aliados, han ayudado a pintar casas, han incentivado a los habitantes del pueblo a iniciar emprendimientos de artesanas con tejidos tradicionales de caa, cocina, belleza, turismo, entre otros.</p>
<p>El viaje transform la perspectiva de Dawn:</p>
<p>Me siento tan privilegiada de haber tenido la oportunidad de visitar Colombia y de aprender sobre la historia de las personas que cultivan un gran chocolate.</p>
<p>Tras usar su chocolate por ms de una dcada, y ahora ver el trabajo que Luker y su proyecto Chocolate Dream estn realizando, me emociona mucho ayudarles a compartir esta historia. Mara de Restrepo fue miembro de la familia fundadora de Luker Chocolate. Todos los sbados, en Manizales, sala a su ventana y regalaba chocolate a las personas sin hogar con el nico propsito de consolarlas y asegurarse de que tuvieran comida en sus estmagos. En ese momento, no se haba investigado sobre la felicidad de comer chocolate, pero lo que ella hizo y el chocolate que regal debi haber reconfortado a muchas personas. Supongo que al usar chocolate Luker hoy en da, estoy haciendo lo mismo, no solo en trminos de apoyar a las familias que se encuentran en la fuente de esta travesa, sino tambin al llevar la felicidad de compartir su fino chocolate con mis clientes. Los suizos llamaron al cacao Theobroma en el siglo XVIII, que significa alimento de los dioses. Me gusta pensar que soy una versin moderna de una diosa de chocolate, y que es mi deber llevar el mejor chocolate a las manos de todas las personas del mundo. Y si puedo hacer esto de una manera en la que s que estoy ayudando y apoyando a las familias que lo proveen, pues mucho mejor.</p>
`,
    autor: { name: 'JOS MALDONADO', avatar: 'https://media.licdn.com/dms/image/C5603AQGWmXeWsufD9Q/profile-displayphoto-shrink_800_800/0?e=1581552000&v=beta&t=sPc6xh78N7oYmS61XLxym_XcKLYzzBJ9PSp5kyFCPQk', details: 'SUSTAINABLE SUPPLY ANALYST', description: 'Sustainable Supply Analyst for Luker Chocolate, focused on agricultural development and sustainability projects of the company\'s value chain.Project leader for organizations of cocoa producers and small- scale farmers.', linkedin: ['https://www.linkedin.com/in/jos%C3%A9-miguel-maldonado-v%C3%A9lez-79a818114/', 'Jose Maldonado'] },
