    title: 'CACAO, BOSQUES Y PAZ',
    cover: 'banner-cacaos-w.jpg',
    date: 'OCTUBRE 2019',
    content: `<p>En Luker Chocolate, todos los das partimos y buscamos crecer desde nuestro propsito superior: hacer del mundo un lugar mejor, ayudando a otras empresas a crear productos con propsito, utilizando nuestro chocolate como una herramienta de cambio. Este futuro y cambio que soamos empieza en las regiones productoras de cacao en Colombia, de donde obtenemos el mejor Cacao Fino y de Aroma. Pero no nos queramos quedar ah. Por eso, hace unos aos, decidimos poner todas nuestras fuerzas y ofrecerle soluciones reales al campo colombiano; de ah nace El Sueo de Chocolate.</p>
<p>El reto es enorme. En Colombia, se produce cacao en 27 de los 32 departamentos, lo que genera una gran dispersin geogrfica en la produccin y un sin nmero de barreras para los productores y sus familias. Adems, las principales regiones cacaoteras de Colombia han sido histricamente zonas de conflicto armado, minera ilegal, narcotrfico etc.; y por desgracia, hoy en da muchas lo siguen siendo. A pesar de que los obstculos se presentaran una y otra vez en el camino, nuestro sueo es ms grande. Sabemos que solos no podemos y que la llave para alcanzar nuestra meta es el trabajo en equipo; solo as lograremos que El Sueo de Chocolate deje algn da de ser una aspiracin.</p>
<img src="/static/media/bosque-necocli.12b8874a.jpg" alt="Forest Necocl">
<p>Un reflejo claro de la unin y los esfuerzos en conjunto es la iniciativa Cacao, Bosques y Paz en Colombia. Este es un proyecto pblico-privado que tiene como objetivo promover modelos productivos de deforestacin cero, que favorezcan la proteccin y restauracin de los bosques en la cadena de suministro del cacao, pero que adems, aporten en la construccin de paz de nuestro pas.</p>
<p>La iniciativa nace en marzo de 2017 cuando se lanza la Declaracin de Intencin para la Iniciativa Global Cacao y Bosques, liderada por la World Cocoa Foundation, The Sustainable Trade Initiative (IDH), organizaciones sin nimo de lucro y ms de 35 empresas del sector privado; quienes firmaron los acuerdos macros para la accin conjunta en Ghana y Costa de Marfil, principalmente. En julio de 2018, Colombia se convierte en el primer pas de Latinoamrica y tercero en el mundo, en adherirse a la iniciativa y firmar su propio acuerdo.</p>
<img src="/static/media/cocoa-forest.dfaf6862.jpg" alt="Cocoa forest">
<p>Nosotros, Luker Chocolate, fuimos la primera empresa del sector privado en creer y firmar la iniciativa Cacao, Bosques y Paz en Colombia; siendo partcipes de la planeacin y estructuracin del plan de accin, el cual fue lanzado oficialmente el pasado mes de septiembre en Bogot. Decidimos ser parte de este proyecto porque sabemos que la base de todo lo que hacemos est en las regiones productoras de cacao, porque esa es nuestra fuente de inspiracin y la oportunidad que tenemos para transformar comunidades.</p>
<p>As entonces, esta iniciativa pone en marcha su plan de accin a 10 aos, liderada por los ministerios de Ambiente y Desarrollo Sostenible, Agricultura y Desarrollo Rural, la industria cacaotera representada por Luker Chocolate y la Federacin Nacional de Cacaoteros (Fedecacao), The Sustainable Trade Initiative (IDH) y la World Resources Institute (WRI), con el apoyo de la World Cocoa Foundation.</p>
<p>Sern 10 aos de trabajo, hombro a hombro, en los cuales se busca alcanzar:</p>
<ul>
<li>45 hitos en total (25 a corto, 12 a mediano y 8 a largo plazo).</li>
<li>76 actividades a 2 aos.</li>
<li>51 indicadores de gestin para las actividades a 2 aos.</li>
<li>Ms de 73 indicadores que medirn el sistema de informacin.</li>
<li>5 grandes resultados a 2030:</li>
</ul>
<ol>
<li>Modelos productivos de cacao con deforestacin cero.</li>
<li>Instrumentos financieros que consoliden modelos de cacao con deforestacin cero.</li>
<li>Sistemas de monitoreo, verificacin y trazabilidad.</li>
<li>Fortalecimiento de la institucionalidad cacaotera para bosques y paz.</li>
<li>Estrategias de comunicacin y transparencia.</li>
</ol>
<p>Para la etapa de implementacin de este proyecto, tambin se unirn las fuerzas de nuevos firmantes como la Embajada de Suiza (SECO), Climate Focus, Wildlife Conservation Society (WCS), Organizacin Alisos, Rainforest Alliance, Lk Foods, Mariana Cocoa Export, The Nature Conservancy (TNC), WWF y el Centro Internacional de Agricultura Tropical (CIAT).</p>
<p>Cacao, Bosques y Paz es un hito muy importante para la cacaocultura en Colombia y todo lo que hay detrs de ella. Ser el primer gran pacto por el desarrollo sostenible de la produccin de cacao en el pas que harn los tres sectores de la economa. Este proyecto ser un aporte muy importante en la proteccin y conservacin de toda la riqueza natural con la que convive el cacao, adems; transformar las realidades que hoy en da viven las familias cacaocultoras del pas, ayudar a superar dificultades y ofrecer un mejor futuro para el campo colombiano.</p>
`,
