    breads: [{ href: '/blog', name: 'Blog' }, { href: '/blog/innovacion', name: 'Innovacion' }],
    url: 'beau-cao-un-chocolate-que-cuida-la-belleza-desde-el-interior',
    cover: 'beucao-banner.jpg',
    title: 'BEAU CAO, UN CHOCOLATE QUE CUIDA LA BELLEZA DESDE EL INTERIOR',
    date: 'NOVIEMBRE 2019',
    content: `<p>Durante ms de 30 aos, la feria comercial anual PLMA (Private Label Manufacturing Association) ha sido el evento ms importante en Estados Unidos donde retailers e industriales obtienen informacin y conocen proveedores para desarrollar sus marcas privadas. En la edicin de este ao, ms de 1.500 empresas de 40 pases exhibirn sus productos y servicios, incluidos 25 pabellones internacionales.</p>
<p>A pesar de que desde hace ms de 5 aos hemos participado en este evento, esta ocasin decidimos presentarnos de una manera diferente, comenzando por nuestro stand; un stand ms experiencial, donde los visitantes podrn vivir la experiencia del Luker Chocolate Market, un espacio donde encontrarn nuestra trayectoria de ms de 110 aos como maestros chocolateros llevada al mundo de algunas de las megatendencias de los alimentos: el mundo premium, tico y saludable.</p>
<p>Adems, este ao decidimos ir ms all y presentarnos al New Product Expo, una plataforma donde solo algunas de las compaas que participarn en la feria podrn exhibir sus ltimas innovaciones en productos y empaques. Los productos sern exhibidos en el Idea SuperMarket, un lugar en la feria que muestra lo que supermercados, hipermercados y retailers de todo tipo, estn haciendo con sus marcas. La seleccin de los productos que se mostrarn aqu est basada en el nivel de innovacin de los mismos, despus de que un jurado probara el producto postulado y de esta manera pudiera comprobar su nivel de innovacin. Un par de semanas despus de haber postulado nuestras ideas, tuvimos la gran noticia de haber sido seleccionados con dos innovaciones: Beau Cao y Protein Pud.</p>
<img src="/static/media/beau-cau.cbf77ddd.jpg" alt="Beau Cao">
<p>Hoy te contaremos de qu se trata Beau Cao. Beau Cao es un producto desarrollado a partir de VitalCao, una lnea de ingredientes funcionales de Luker Chocolate la cual ofrece cocoas en polvo con los mejores beneficios provenientes del cacao. Tras 11 aos de intensa investigacin y gracias a todas las posibilidades que nos ofrece el estar presentes en toda la cadena de valor de nuestro chocolate, desde el grano de cacao hasta el producto final, desarrollamos un mtodo de produccin nico, con patente en proceso, en donde controlamos la prcticas posteriores a la cosecha (fermentacin y secado) y el proceso industrial para obtener una gran cantidad de antioxidantes del cacao. Todo esto ha dado como resultado un portafolio de productos naturales que aportan beneficios a la salud en la era moderna.</p>
<p>Una de las innovaciones ms fuertes de VitalCao es su capacidad de proteger las clulas del cuerpo de los rayos UV. Las propiedades antioxidantes de VitalCao previenen la formacin de radicales libres que se generan a partir de la exposicin a rayos ultravioleta. Partiendo de los beneficios de VitalCao y entendiendo que la belleza es un proceso que se debe cultivar de adentro hacia afuera, en Luker Chocolate decidimos lanzar al mercado Beau Cao, un producto que no solamente es indulgente sino que cuida de tu piel reconstituyndola desde el interior.</p>
<p>La frmula de Beau Cao ayuda a proteger las clulas hasta un 60% ms que un chocolate regular, lo que significa que tu cuerpo se protege de los rayos del sol desde su interior. Adicionalmente, ayuda a reducir inflamaciones que se puedan presentar en tejidos de la piel hasta un 26% ms.</p>
<p>En Luker Chocolate hicimos un estudio mdico de la mano de Ikemia IUCT (grupo empresarial espaol de alta tecnologa orientado a desarrollar y explotar nuevos productos) que probara cientficamente los beneficios del consumo diario de Beau Cao. Durante 1 ao, 10 sujetos saludables, con edad promedio de 35.4 aos, con piel clara tipo II (de acuerdo a la escala de FitzPatrick), consumieron de 4 a 6 gramos de VitalCao. Luego expusimos reas de 11 cm de piel sin broncear del lado interno del antebrazo a dosis crecientes de radiacin solar simulada (SSR, simulador solar multipuerto modelo 601, luz solar, Glenside, PA). Con el estudio encontramos como resultado un incremento significativo en el efecto foto protector de la piel, efecto directamente relacionado a la alta actividad antioxidante del producto.</p>
<p>Estudios clnicos realizados por nuestra rea de Investigacin y Desarrollo, prueban que la versin de Beau Cao con el 45% de cacao, tiene 75% ms flavanoles (componentes que se encuentran en algunos alimentos y que ayudan al organismo a protegerse del dao producido por agentes oxidantes como los rayos ultravioletas, la polucin, sustancias qumicas presentes en los alimentos, etc.) que un chocolate de leche regular con el mismo contenido de cacao. Asimismo, la versin del 58% y del 70% de contenido de cacao (esta ltima es la que presentaremos en PLMA), contienen 57% y 47% ms contenido de flavanoles respectivamente que un chocolate regular.</p>
<p>En esta infografa encontrars ms informacin sobre nuestro producto:</p>
<img src="/static/media/ficha-beau-cao.4216f6f5.jpg" alt="Beau Cao Infographic">
<p>Estamos orgullosos de lanzar por primera vez un producto completamente innovador en el sector de la belleza y de participar en el Idea Supermarket de PLMA.</p>
<p>Si te interesa Beau Cao, anmate a trabajar de la mano con nosotros; esta innovacin podra ser tuya y hacer parte de tu portafolio de marca privada. Si quieres conocer el producto, acrcate al Luker Chocolate Market que se encontrar en el North Hall, stand F3011, del 17 al 19 de noviembre en el Rosemont Convention Center de Chicago.</p>
<p>Te esperamos!</p>
<small>*Estos estudios fueron realizados por investigaciones internas de Casa Luker S.A.. La declaracin de beneficios de este producto deber ser aprobada por las entidades regulatorias competentes segn el pas y con el lleno de los requisitos correspondientes.</small>
`,
