    title: 'PROTEIN PUD, EL SNACK PERFECTO',
    date: 'NOVIEMBRE 2019',
    content: `<p>En el blog de la semana pasada, les contamos sobre nuestra participacin en la feria PLMA (Private Label Manufacturing Association), donde nuestros clientes y asistentes al evento podrn vivir la experiencia del Luker Chocolate Market. Adems, presentamos una de nuestras ltimas innovaciones en producto: Beau Cao, un chocolate que cuida la belleza desde el interior, el cual exhibiremos en el New Product Expo, una plataforma donde solo algunas de las compaas que participarn en la feria podrn exhibir sus ltimas innovaciones.</p>
<p>Hoy, el protagonista de este blog es el Protein Pud, el segundo producto innovador que exhibiremos en ese mismo espacio. Una formulacin que hemos desarrollado y puesto a disposicin de nuestros clientes quienes la deseen tener en su portafolio de marca privada. Aqu les contamos cmo fue la creacin de este producto.</p>
<img src="/static/media/protein-pud.e083a995.jpg" alt="Protein pud">
<p>Actualmente, las personas tienen menos tiempo para estar en sus hogares y preparar las comidas del da. A esto, se le suma que hay otras prioridades como su vida social y el cuidado personal. Es por esto que, los snacks se han convertido en la solucin perfecta para alargar el tiempo de consumo entre una comida y la otra, pero al mismo tiempo generan satisfaccin.</p>
<p>Partiendo del comportamiento de estos consumidores, es que decidimos desarrollar el Protein Pud: un pudn instantneo de proteina de soya endulzado con stevia que no necesita refrigeracin. Un snack perfecto, pues su preparacin es rpida, es saludable, delicioso y te hace sentir satisfecho. Su formulacin innovadora permite que tan solo agregando un poco de agua fra se obtenga la textura deseada. Adems, es un producto hecho a base de plantas, por lo tanto es apto para veganos. Y para hacer que la experiencia del Protein Pud sea an ms memorable, se le pueden agregar diferentes toppings como frutos secos, fruta deshidratada o fresca, nibs de cacao, etc.</p>
<p>Fueron ms de 6 meses en los que nuestro equipo de Investigacin y Desarrollo trabaj en bsqueda de la formulacin adecuada para crear el balance entre textura y sabor. Primero, se buscaron las materias primas que nos dieran la sensacin cremosa y suave que queramos que tuviera el producto. Luego, estaba el reto de que este mantuviera el sabor exquisito del chocolate hecho con Cacao Fino de Aroma que nos caracteriza y que; adems; mantuviera la textura. Sin embargo, el reto ms importante fue encontrar la forma en que todo lo anterior se mantuviera en una preparacin hecha con agua fra, sin que el pudn se resquebrajara o se volviera granuloso.</p>
<p>Ms all de ser indulgente e innovador, el Protein Pud es buena fuente de protena, uno de los nutrientes con mayor presencia en el cuerpo humano y que es vital en la formacin de msculos y cumple un papel fundamental en el buen funcionamiento del sistema inmune. Adems, aporta calcio, un complejo de vitaminas como la vitamina A, vitamina B2, vitamina B12; y minerales como hierro, zinc y cobre.</p>
<p>Con la frmula perfeccionada, la pregunta final era cmo mostrarle al mundo y a nuestros clientes esta innovacin? Para esto, desarrollamos dos presentaciones de producto. La primera, on the go, es la presentacin individual, diseada para las personas que quieran tener un snack y consumirlo en cualquier momento y lugar. La segunda, home presentation, es ideal para preparar algo delicioso y saludable mientras se est en casa alejado de la rutina.</p>
<p>En esta infografa encontrars ms informacin sobre nuestro producto:</p>
<img src="/static/media/ficha-protein-pud.b6ddb06e.jpg" alt="Protein pud infographic">
<p>En Luker Chocolate nos sentimos muy orgullosos de presentar nuestras ltimas innovaciones y que, tanto el Beau Cao como el Protein Pud, estn presentes en el Idea Supermarket de PLMA.</p>
<p>Si te interesa el Protein Pud, anmate a trabajar de la mano con nosotros; esta innovacin podra ser tuya y hacer parte de tu portafolio de marca privada. Si quieres conocer el producto, acrcate al Luker Chocolate Market que se encontrar en el North Hall, stand F3011, del 17 al 19 de noviembre en el Rosemont Convention Center de Chicago. Si no vas a asistir a PLMA y te interesa nuestra innovacin, escrbenos.</p>
<p>Te esperamos!</p>
`,
    autor: { name: 'JOHANNA VERDUGO', avatar: 'https://media.licdn.com/dms/image/C4E03AQEBjQMIzBoosQ/profile-displayphoto-shrink_800_800/0?e=1581552000&v=beta&t=PJFM_xfyNj0lFBOFrXEG8yuGX72o0fnMupSc49dazWw', details: 'Brand and Communications Analyst', description: 'Social communicator with emphasis on journalism from the Pontificia Universidad Javeriana de Bogot, Colombia.', linkedin: ['https://www.linkedin.com/in/johanna-verdugo-romero-0564a9151/', 'Johanna Verdugo Romero'] },
